body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* fallback */
@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(https://fonts.gstatic.com/s/materialicons/v47/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');
}

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -moz-font-feature-settings: 'liga';
    -moz-osx-font-smoothing: grayscale;
}

body {
    text-align: center;
}

a {
    cursor: pointer;
}

.app .breadcrumb:last-child {
    color: rgba(255,255,255,0.7);
}

.app .breadcrumb.active {
    color: #fff;
}

.level-one-objects-grid img {
    object-fit: cover;
    width: 100%;
    max-width: 250px;
    max-height: 110px;
    cursor: pointer;
}

.level-one-objects-grid .row {
    display: flex;
    justify-content: center;
}

.level-one-objects-grid .row .col.s4 {
    display: flex;
    margin-left: 0;
}

.level-one-objects-grid .image-grid {
    margin-top: 2em;
}

.level-one-objects-grid .selected {
    border: 3px solid #cccbcb;
    opacity: 0.7;
}


@media screen and (min-width: 1200px) {
    .level-one-objects-grid .row .col.s4 {
        width: 26%;
    }
}

@media screen and (min-width: 600px) {
    .level-one-objects-grid img {
        max-height: 250px;
    }
}
.level-two-objects {
    margin-top: 10px;
}

.level-two-objects img {
    object-fit: cover;
    object-position: top;
    height: 150px;
    width: 150px;
}

.level-two-objects .col.s6.images {
    text-align: left;
}

.descriptions-collection {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.description {
    border: 1px solid #e0e0e0;
    padding: 0.5em;
    height: 155px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.correct {
    border: 1px solid #009688;
}

.description i.small {
    font-size: 1.2rem;
    position: absolute;
    top: 0.5em;
    right: 0.5em;
    color: #009688;
}

@media screen and (min-width: 600px) {
    .level-two-objects img {
        max-height: 250px;
    }

    .level-two-objects .descriptions-collection {
        max-width: 300px;
        margin-left: auto;
        margin-right: 0;
    }
}
